<template>
  <div>
    <div>
        <button class="w3-button w3-block w3-green" v-if="accounts.length === 0" @click="connectToMetamask()">Connect to Metamask</button>
        <button class="w3-button w3-block w3-black" v-else @click="disconnect()">Disconnect</button>
    </div>
    <div class="w3-container">
        <div class="w3-row-padding w3-blue w3-card w3-margin">
            <div class="w3-half"><h4>Account:</h4></div>
            <div class="w3-half"><h4 class="w3-text-black">&nbsp; <code>{{ accounts[0] }}</code></h4></div>
        </div>
        <div class="w3-row-padding">
            <h3 class="w3-panel w3-half">NFT Minting Page</h3>
            <div v-if="accounts.length > 0" class="w3-panel w3-half">
                <button @click="mintNft()" class="w3-button w3-red w3-round">Mint NFT</button>
            </div>
        </div>
        <div v-if="errorMessage.length > 0" class="w3-panel w3-pale-red w3-padding">
            <div class=" w3-text-red">{{errorMessage}}
            <button @click="clearError()" class="w3-button w3-red w3-round w3-right" title="Close">&times;</button>
            </div>
        </div>
        <div class="w3-panel w3-row-padding">
            <div v-for="(nft, index) in nfts" :key="index" class=" w3-third">
                <div class="w3-card w3-round w3-pale-green w3-padding w3-margin">
                    <h5 class="w3-text-blue">Token #{{ nft.tokenId }}</h5>
                    <div class="w3-row-padding">
                        <div class="w3-half"><img :src="nft.image" height="150"/></div>
                        <div class="w3-half">
                            <div v-for="(attr, index2) in nft.attributes" :key="index2" class="nft-attr">
                                <strong>{{ attr.trait_type }}</strong>: {{ attr.value }}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
import { ethers, utils } from 'ethers'
import erc721Abi from '../contract-abis/erc721.json'

export default {
  data() {
    return {
      provider: null,
      accounts: [],
      nftContract: null,
      nfts: [],
      errorMessage: ""
    }
  },
  methods: {
    disconnect() {
      this.provider = null
      this.accounts = []
      this.nftContract = null
    },
    async connectToMetamask() {
      this.provider = new ethers.providers.Web3Provider(window.ethereum)
      this.accounts = await this.provider.send('eth_requestAccounts', [])

      this.createContractInstance()
    },
    createContractInstance() {
      this.nftContract = new ethers.Contract('0xeF6bD89514BF93E714A870871284d06Dc84D53A7', erc721Abi)
      this.nftContract = this.nftContract.connect(this.provider)
      this.getMintedNfts()
    },
    async mintNft() {
        var signer = this.provider.getSigner()
        var nftContractWithSigner = this.nftContract.connect(signer)
        try {
            var transaction = await nftContractWithSigner.mint({ value: utils.parseEther("0.01") })
            await transaction.wait()
            window.alert('Minted new NFT!')
            this.errorMessage = ""
        } catch(error) {
           this.errorMessage = "Error: "+error.data.message
        }
        this.getMintedNfts()
    },
    async getMintedNfts() {
        var _nfts = []
        var eventFilter = this.nftContract.filters.Minted()
        var logs = await this.nftContract.queryFilter(eventFilter)
        for(var i = 0; i < logs.length; i++) {
            var tokenId = logs[i].args.tokenId
            var tokenUri = await this.nftContract.tokenURI(tokenId)
            var _response = await fetch(tokenUri)
            var tokenMetadata = await _response.json()
            tokenMetadata.tokenId = tokenId
            _nfts.push(tokenMetadata)
        }
        this.nfts = _nfts
    },
    clearError() {
        this.errorMessage = "";
    }
  }
}
</script>
